import React, { useState, useEffect } from "react";
import firebase, { signOut } from "./services/FirebaseConfig";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";

import Navbar from "./Navbar";
import Users from "./Users";
import SignIn from "./SignIn";

export default function App() {
  const initialState = {
    user: null,
    level: "normal",
  };
  const [session, setSession] = useState(initialState);

  useEffect(() => {
    authCheck();
  }, []);

  const authCheck = async () => {
    firebase.auth().onAuthStateChanged((user) => {
      if (!user) {
        setSession(initialState);
      }
      fetch("https://raningu-api.glitch.me/data/auth/admin_login", {
        method: "POST",
        cache: "no-cache",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(user),
      })
        .then((res) => res.json())
        .then((result) => {
          if (result.result === "success") {
            setSession({ level: "admin" });
          } else if (result.detail === "access") {
            signOut();
            console.log("access");
            return setSession({ level: "access" });
          } else {
            signOut();
            console.log("error");
            return setSession({ level: "error" });
          }
        });

      setSession({ user: user });
    });
  };
  return (
    <Router>
      <div className="app-container">
        <Navbar session={session} />
        <Routes>
          <Route exact path="/"></Route>
          <Route exact path="/docs"></Route>
          <Route
            exact
            path="/signin"
            element={
              session.level !== "admin" ? (
                <SignIn session={session} />
              ) : (
                <Navigate to="/admin" />
              )
            }
          ></Route>
          <Route
            exact
            path="/admin"
            element={
              session.level === "admin" ? (
                <Users session={session} />
              ) : (
                <Navigate to="/" />
              )
            }
          ></Route>
        </Routes>
      </div>
    </Router>
  );
}
